// =========================== >>
// Buttons
// =========================== >>

.btn {
  white-space: normal;
  display: inline-block;
  text-decoration: none;
  text-align: center;

  cursor: pointer;
  border-radius: 5px;
  padding: 12px 18px;
  background-clip: padding-box;
  border: 0px;
  text-transform: uppercase;
  font-size: 13px;
  font-weight: 600;

  &:focus,
  &:hover {
    box-shadow: none;
  }

  &:active {
    box-shadow: none;
  }

  &:disabled {
    cursor: not-allowed;
  }

  &.btn-block {
    display: block;
    width: 100%;
  }

  &.btn-primary {
    background: $ui_color_primary;
    color: $ui_header_color;
  }

  &.btn-secondary {
    background: $ui_color_secondary;
    color: $ui_header_color;
  }
}
