// =========================== >>
// Widget Suggestion
// =========================== >>

.filipay__suggestion {
  position: absolute;
  bottom: 55px;
  left: 0;
  width: 100%;
  background: #eeeeee;
  z-index: 11;
  max-height: 150px;
  height: auto;

  display: flex;
  flex-direction: column;

  &__heading {
    font-size: 15px;
    background: #19b0de;
    text-transform: uppercase;
    color: #ffffff;
    padding: 10px 20px;
  }

  &__content {
    padding: 5px 20px;
    max-height: 150px;
    height: 100%;
    display: flex;
    flex-direction: column;
    overflow-y: auto;

    &__item {
      font-size: 14px;
      margin: 12px 0px;
      color: #19b0de;
      display: flex;
      cursor: pointer;

      i {
        color: #bbbbbb;
        margin-top: 3px;
        margin-right: 8px;
      }
    }
  }
}
