// =========================== >>
// Widget Footer: Attachment
// =========================== >>

.footer-att {
  display: flex;
  margin: 0px 20px 0px 20px;
  overflow-x: auto;
  padding-top: 15px;
}

.footer-att-item {
  background: #eee;
  border-radius: 5px;
  padding: 10px 15px;
  position: relative;
  display: flex;
  align-items: center;
  margin: 5px 5px 10px 0px;
  flex-grow: 0;
  flex-shrink: 0;
  flex-basis: 150px;
  width: 150px;

  &:last-child {
    margin-right: 0px;
  }

  &.invalid {
    border: 1px solid red;
    color: red;

    .footer-att-item-icon {
      display: none;
    }
  }
}

.footer-att-item-icon {
  padding: 10px 7px 7px;
  background: #ccc;
  border-radius: 5px;
  margin-right: 10px;
}

.footer-att-item-name {
  overflow: hidden;

  span {
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 2;
    display: -webkit-box;
  }
}

.footer-att-item-remove {
  border-radius: 100%;
  background: #ccc;
  cursor: pointer;
  padding: 3px;
  height: 15px;
  width: 15px;

  display: flex;
  align-items: center;
  justify-content: center;

  position: absolute;
  top: -5px;
  right: -5px;
}
