// =========================== >>
// Widget Body
// =========================== >>

.body {
  z-index: 100;
  width: 100%;
  height: 100%;

  background: #ffffff;
  padding: 25px;
  overflow-y: auto;
  overflow-x: hidden;

  position: relative;
  word-break: break-word;

  display: flex;
  flex-direction: column-reverse;
}

.body-greeting {
  padding: 20px;

  img {
    width: 100%;
    height: auto;
    padding: 0px 20px;
  }
}

.bubble {
  display: flex;
  margin: 10px 0px;
  width: 100%;

  .bubble-content {
    border-radius: 4px;
    font-size: 14px;
    position: relative;
    width: auto;
    line-height: 18px;
    max-width: 100%;
    display: flex;
    align-items: center;

    .msg-actions {
      opacity: 0;

      .action {
        cursor: pointer;
        padding: 5px;
        background: #eee;
        border-radius: 100%;
        border: 0;
      }
    }
    .msg-group {
      .msg-rpy {
        padding: 8px 10px 8px 18px;
        font-size: 12px;
        position: relative;

        &::after {
          content: "";
          position: absolute;
          left: 10px;
          top: 8px;
          width: 3px;
          height: calc(100% - 16px);
        }

        p:first-child {
          margin-top: 0px;
        }

        p:last-child {
          margin-bottom: 0px;
        }
      }
      .msg-dts {
        padding: 10px;

        p:first-child {
          margin-top: 0px;
        }

        p:last-child {
          margin-bottom: 0px;
        }
      }
      .msg-att {
        display: flex;
        overflow-x: auto;

        &-item {
          border-radius: 5px;
          overflow: hidden;
          position: relative;
          display: flex;
          align-items: center;
          flex-grow: 0;
          flex-shrink: 0;
          flex-basis: 200px;
          width: 200px;

          &-image {
            padding: 0;

            img {
              object-fit: cover;
              display: block;
              height: 100%;
              width: 100%;
            }
          }

          &-file {
            display: flex;
            align-items: center;
            padding: 10px 15px;
            width: 100%;
          }
        }

        &-item-icon {
          cursor: pointer;
          padding: 10px 7px 7px;
          background: #ccc;
          border-radius: 5px;
          margin-right: 10px;
        }

        &-item-name {
          cursor: pointer;
          overflow: hidden;

          span {
            -webkit-box-orient: vertical;
            -webkit-line-clamp: 2;
            display: -webkit-box;
          }

          &:hover {
            text-decoration: underline;
          }
        }

        &-item-remove {
          border-radius: 100%;
          background: #ccc;
          cursor: pointer;
          padding: 3px;
          height: 15px;
          width: 15px;

          display: flex;
          align-items: center;
          justify-content: center;

          position: absolute;
          top: -5px;
          right: -5px;
        }
      }
    }
  }

  &.from-client {
    padding-left: 15%;
    justify-content: flex-end;

    .bubble-content {
      flex-direction: row;

      .msg-actions {
        margin-right: 10px;
      }
      .msg-group {
        color: $ui_client_color;

        .msg-rpy {
          background: #f5f5f5;
          float: right;

          &::after {
            background: #ccc;
          }
        }
        .msg-dts {
          background: #f5f5f5;
          clear: both;
        }
        .msg-att {
          float: right;

          &-item {
            background: #eee;
          }
        }
      }
    }
  }
  &.from-admin {
    padding-right: 15%;
    justify-content: flex-start;

    .bubble-content {
      flex-direction: row-reverse;

      .msg-actions {
        margin-left: 10px;
      }
      .msg-group {
        color: $ui_header_color;

        .msg-rpy {
          background: rgba($color: #f68a1f, $alpha: 0.75);
          float: left;

          &::after {
            background: $ui_color_secondary;
          }
        }
        .msg-dts {
          background: $ui_color_secondary;
          clear: both;
        }
        .msg-att {
          float: left;

          &-item {
            background: $ui_color_secondary;
          }
        }
      }
    }
  }
  &.session {
    font-size: 12px;
    color: gray;
    text-align: center;
    margin: 28px 0;

    .bubble-message {
      margin: 0 auto;
    }
  }
  &.error {
    padding-right: 15%;
    justify-content: flex-start;

    .bubble-content {
      .msg-group {
        color: #ffffff;

        .msg-dts,
        .msg-att {
          background: #ed2129;
        }
      }
    }
  }

  &:hover {
    .bubble-content {
      .msg-actions {
        opacity: 1;
      }
    }
  }
  &:last-child {
    margin-bottom: 0px;
  }
}
