.emoji-picker {
  width: 100% !important;
  border: 0 !important;

  #Categories {
    .category {
      padding: 10px 5px !important;
    }
  }

  #InputSearch {
    .container-search {
      margin: 0 !important;
      padding: 16px !important;

      input[type="text"] {
        border-radius: 3px !important;
        padding: 10px 16px !important;
      }
    }
  }

  #Emojis {
    .container-emoji {
      .grid-emojis {
        padding: 0px 10px 10px 10px !important;
        margin: 0px !important;
      }
    }
  }
}
