// =========================== >>
// Animations
// =========================== >>

@keyframes lds-dual-ring {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

@keyframes open {
  0% {
    transform: scale(0);
    opacity: 0;
  }
  100% {
    transform: scale(1);
    opacity: 1;
  }
}

@keyframes close {
  0% {
    transform: scale(0) rotate(90deg);
    opacity: 0;
  }
  100% {
    transform: scale(1) rotate(180deg);
    opacity: 1;
  }
}
