// =========================== >>
// Margin's
// =========================== >>

$margin_sizes: mt mr mb ml my mx m;

@each $size in $margin_sizes {
  $class-slug: $size;

  @for $i from 0 through 4 {
    .#{$class-slug}-#{$i} {
      @if $class-slug == "mt" {
        margin-top: 0px + ($i/2);
      }

      @if $class-slug == "mr" {
        margin-right: 0px + ($i/2);
      }

      @if $class-slug == "mb" {
        margin-bottom: 0px + ($i/2);
      }

      @if $class-slug == "ml" {
        margin-left: 0px + ($i/2);
      }

      @if $class-slug == "my" {
        margin-top: 0px + ($i/2);
        margin-bottom: 0px + ($i/2);
      }

      @if $class-slug == "mx" {
        margin-left: 0px + ($i/2);
        margin-right: 0px + ($i/2);
      }

      @if $class-slug == "m" {
        margin: 0px + ($i/2);
      }
    }
  }
}

// =========================== >>
// Padding's
// =========================== >>

$padding_sizes: pt pr pb pl py px p;

@each $size in $padding_sizes {
  $class-slug: $size;

  @for $i from 0 through 4 {
    .#{$class-slug}-#{$i} {
      @if $class-slug == "pt" {
        padding-top: 0px + ($i/2);
      }

      @if $class-slug == "pr" {
        padding-right: 0px + ($i/2);
      }

      @if $class-slug == "pb" {
        padding-bottom: 0px + ($i/2);
      }

      @if $class-slug == "pl" {
        padding-left: 0px + ($i/2);
      }

      @if $class-slug == "py" {
        padding-top: 0px + ($i/2);
        padding-bottom: 0px + ($i/2);
      }

      @if $class-slug == "px" {
        padding-left: 0px + ($i/2);
        padding-right: 0px + ($i/2);
      }

      @if $class-slug == "p" {
        padding: 0px + ($i/2);
      }
    }
  }
}
