// =========================== >>
// Widget Toggler
// =========================== >>

.toggler {
  background: $ui_color_secondary;

  cursor: pointer;

  position: fixed;
  right: 20px;
  bottom: 20px;

  display: flex;
  justify-content: center;
  align-items: center;

  width: 100%;
  height: 100%;
  max-width: 60px;
  max-height: 60px;

  border-radius: 10px 40px 40px 40px;
  transition: all 300ms ease-in-out;

  &.open {
    border-radius: 40px 10px 40px 40px;
  }
}

.toggler-open {
  width: 30px;
  animation-name: open;
  animation-duration: 400ms;
}

.toggler-close {
  width: 20px;
  animation-name: close;
  animation-duration: 400ms;
}
