.lds-dual-ring {
  display: block;
  width: 25px;
  height: 25px;
}

.lds-dual-ring:after {
  content: " ";
  display: block;
  width: 15px;
  height: 15px;
  border-radius: 50%;
  border: 5px solid #fff;
  border-color: #424242 transparent #424242 transparent;
  animation: lds-dual-ring 1.2s linear infinite;
}
