// =========================== >>
// Widget Start
// =========================== >>

.welcome {
  display: flex;
  flex-direction: column;
  height: 100%;
}

.welcome-banner {
  background: $ui_color_secondary;
  padding: 40px 30px 50px;

  .banner-icons {
    display: flex;
    justify-content: space-between;

    .logo {
      background: #fff;
      border-radius: 8px;
      display: flex;
      align-items: center;
      justify-content: center;
      padding: 4px 1px;
      height: auto;
      width: 60px;
      margin-bottom: 28px;
      padding: 0.75rem;
    }

    .close {
      background: $ui_color_secondary;
      filter: brightness(0.85);
      border-radius: 100%;
      cursor: pointer;
      height: 23px;
      width: 23px;
      padding: 5px;
      margin-top: -15px;
      margin-right: -10px;

      @media (min-width: 768px) {
        display: none;
      }
    }
  }

  .banner-titles {
    .title {
      color: #fff;
      font-weight: bold;
      font-size: 18px;
      margin-bottom: 14px;
    }

    .sub-title {
      color: rgba($color: #fff, $alpha: 0.85);
      font-size: 14px;
      margin-bottom: 14px;
    }
  }
}

.welcome-create {
  box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.15);
  border-left: 3px solid $ui_color_secondary;
  background: #fff;
  border-radius: 5px;
  padding: 20px;
  margin: -35px 30px 20px 30px;
}

.welcome-ticket {
  background: #ffffff;
  border-left: 3px solid $ui_color_secondary;
  box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.15);
  border-radius: 5px;
  padding: 20px;
  margin: 0px 30px 20px 30px;

  .history {
    margin: 0 0 10px;
  }

  .list {
    background: #ffffff;
    border-left: 3px solid #ffffff;
    border-radius: 5px;
    box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1), 0 1px 2px 0 rgba(0, 0, 0, 0.06);
    cursor: pointer;
    padding: 15px;
    position: relative;
    display: flex;
    align-items: center;

    .list-meta {
      flex-grow: 1;
      padding-right: 10px;

      .title {
        margin: 0 0 5px;
        word-break: break-all;
      }
      .date {
        margin: 0;
        color: #7d7d7d;
        font-size: 12px;
      }
    }
    .list-status {
      .counter {
        position: absolute;
        top: -5px;
        right: -5px;
        margin: 0;
        background: #dc3545;
        border-radius: 100%;
        color: #fff;
        height: 18px;
        width: 18px;
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: 10px;
      }
      .status {
        margin: 0;
        font-size: 10px;
        padding: 1px 5px;
        color: #ffffff;
        border-radius: 17px;
        text-transform: uppercase;
        background: $ui_color_secondary;
        text-align: center;
      }
    }

    &:hover {
      background: #eeeeee;
      border-left-color: $ui_color_secondary;
    }

    &:not(:last-of-type) {
      margin: 0px 0px 15px;
    }
  }

  .no-result {
    color: #7d7d7d;
    margin-bottom: 0px;
  }
}

.welcome-powered {
  color: #7d7d7d;
  font-size: 12px;
  text-align: center;
  margin-top: auto;
  padding: 20px;
}
