.footer {
  .reply-to-wrapper {
    padding: 16px 16px 0px 16px;
  }

  .reply-to-meta {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 10px;

    .replying-to {
      margin: 0px;
    }

    .close {
      cursor: pointer;
      border-radius: 100%;
      background: #ccc;
      padding: 2px;
    }
  }

  .reply-to-body {
    display: flex;

    .quote {
      background: #ccc;
      width: 3px;
    }

    .content {
      background: #eee;
      padding: 13px;
      flex-grow: 1;

      p:first-child {
        margin-top: 0px;
      }

      p:last-child {
        margin-bottom: 0px;
      }
    }
  }
}
