// =========================== >>
// Widget Header
// =========================== >>

.header {
  display: flex;
  align-items: center;
  background: $ui_color_secondary;
  position: relative;
  padding: 25px 20px;
}

.header-back,
.header-close {
  border-radius: 8px;
  background: #ee8319;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0.5rem;

  &:hover {
    background: #e17a14;
  }
}

.header-meta {
  display: flex;
  align-items: center;
  width: 100%;
  margin-left: 10px;

  .header-logo {
    height: 50px;
    width: 50px;
    border-radius: 100%;
    background: #ffffff;
    overflow: hidden;
    border: 3px solid #ffffff;

    img {
      object-fit: contain;
      width: 100%;
      height: 100%;
    }

    &-initial {
      font-size: 28px;
      font-weight: bold;
      height: 100%;
      width: 100%;
      background: #99bd3c;
      display: flex;
      align-items: center;
      justify-content: center;
      color: #fff;
    }
  }

  .header-title {
    flex-basis: calc(100% - 93px);
    color: $ui_header_color;
    width: calc(100% - 60px);
    margin-left: 15px;
    margin-right: 15px;

    .header-title {
      font-size: 18px;
      font-weight: 100;
      margin: 0px 0px 5px 0px;
    }

    .header-title-sub {
      font-size: 12px;
      font-weight: 100;
      margin-bottom: 0px;
      display: flex;
      align-items: center;
    }

    .header-title-online-indicator,
    .header-title-offline-indicator {
      height: 10px;
      width: 10px;
      border-radius: 100%;
      margin-right: 5px;
      flex-grow: 0;
      flex-shrink: 0;
      flex-basis: 10px;
    }

    .header-title-online-indicator {
      background: #31a24c;
    }

    .header-title-offline-indicator {
      background: #808080;
    }
  }
}

.header-close {
  position: absolute;
  top: 15px;
  right: 15px;
}
