// =========================== >>
// Widget Footer: Form
// =========================== >>

.footer-form {
  display: flex;
  align-items: center;
  padding: 15px;

  button {
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    background: none;
    border: none;
    height: 25px;
    width: 25px;
  }
}

.footer-input {
  flex-grow: 1;
  display: flex;
  align-items: center;
  justify-content: center;

  .editor {
    margin-left: 5px;
    width: 100%;
    word-break: break-all;
    max-height: 108px;
    overflow: auto;
  }
}

.footer-control {
  display: flex;
  align-items: center;
  justify-content: center;

  input[type="file"] {
    display: none;
  }
}
