// =========================== >>
// Widget Wrapper
// =========================== >>

.wrapper {
  background: #ffffff;

  cursor: default;

  position: fixed;
  right: 0px;
  bottom: -20px;
  top: 0px;

  display: flex;
  flex-direction: column;
  overflow-y: auto;

  width: 100%;
  height: 100%;
  opacity: 0;
  visibility: hidden;

  box-shadow: 0 25px 50px -12px rgba(0, 0, 0, 0.25);
  transition: all 200ms ease-in;

  &.open {
    z-index: 100000;
    bottom: 0px;
    opacity: 1;
    visibility: visible;
  }
}
